.size {
    /* margin: 0; */
    height: 20vh;
    background-color: black;
    border-bottom: 4px;
    border-bottom-style: solid;
    border-color: orange
}
.header,
.userHeader {
    font-family: "Press Start 2P";
    margin-top: -5vh;
    padding: 2em 0 0 0;
    background-color: black;
    color: orange;
    text-align: center;
    letter-spacing: .5em;
}

.header {
    font-size: 3em;
}

.userHeader {
    font-size: 2em;
}

.inst {
    font-family: 'Press Start 2P';
    text-align: center;
    color: orange;
    letter-spacing: 5px;
    font-size: 12px
}

@media screen and (max-width: 500px) {
    /* .size {
        height: auto;
    } */
    .header {
        font-size: 2em;
    }

    .userHeader {
        font-size: 1em;
    }
    
}


